import { AfterViewInit, Component, Input } from '@angular/core';
import { BcMenuTabsVerticalBehavior, BcMenuTabsVerticalItemConfig } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

@Component({
  selector: 'bc-menu-tabs-vertical',
  templateUrl: './bc-menu-tabs-vertical.component.html',
})
export class BcMenuTabsVerticalComponent implements AfterViewInit {
  /**
   * Id del elemento
   *
   * @memberof BcMenuTabsVerticalComponent
   */
  @Input() componentId = this.bcHelperService.getId(this);
  /**
   * Listado de items a renderizar en el menú
   *
   * @memberof BcMenuTabsVerticalComponent
   */
  @Input() menuItems: BcMenuTabsVerticalItemConfig[] = [];
  /**
   * Orientación del menú, puede ser: left o right.
   *
   * @memberof BcMenuTabsVerticalComponent
   */
  @Input() orientation: 'left' | 'right' = 'right';

  bcMenuTabsVerticalBehavior: BcMenuTabsVerticalBehavior;

  constructor(private bcHelperService: BcHelperService) {}

  ngAfterViewInit(): void {
    this.bcMenuTabsVerticalBehavior = new BcMenuTabsVerticalBehavior();
    this.bcMenuTabsVerticalBehavior.setId(this.componentId);
    this.bcMenuTabsVerticalBehavior.setUp();
  }
}
